'use client';

import { useState, useEffect } from 'react';
import Image from 'next/image';
import dynamic from 'next/dynamic';
import { Search, Loader2, MapPin, Globe, Server, Shield, Network } from 'lucide-react';
import 'leaflet/dist/leaflet.css';

// --- SETUP PETA (CLIENT-SIDE ONLY) ---
const MapContainer = dynamic(
  () => import('react-leaflet').then((mod) => mod.MapContainer),
  { ssr: false, loading: () => <div className="flex h-full w-full animate-pulse items-center justify-center rounded-xl bg-slate-100 text-slate-400">Loading Map...</div> }
);
const TileLayer = dynamic(
  () => import('react-leaflet').then((mod) => mod.TileLayer),
  { ssr: false }
);
const Marker = dynamic(
  () => import('react-leaflet').then((mod) => mod.Marker),
  { ssr: false }
);
const Popup = dynamic(
  () => import('react-leaflet').then((mod) => mod.Popup),
  { ssr: false }
);

interface IpData {
  ip: string;
  city: string;
  region: string;
  country: string;
  isp: string;
  asn: string;
  timezone: string;
  lat: number;
  lon: number;
  error?: string;
}

export default function IpCheckerPage() {
  const [inputIp, setInputIp] = useState('');
  const [data, setData] = useState<IpData | null>(null);
  const [loading, setLoading] = useState(false);
  const [customIcon, setCustomIcon] = useState<any>(null);

  useEffect(() => {
    const initLeaflet = async () => {
      const L = (await import('leaflet')).default;
      const icon = L.icon({
        iconUrl: "https://cdnjs.cloudflare.com/ajax/libs/leaflet/1.7.1/images/marker-icon.png",
        iconRetinaUrl: "https://cdnjs.cloudflare.com/ajax/libs/leaflet/1.7.1/images/marker-icon-2x.png",
        shadowUrl: "https://cdnjs.cloudflare.com/ajax/libs/leaflet/1.7.1/images/marker-shadow.png",
        iconSize: [25, 41],
        iconAnchor: [12, 41],
        popupAnchor: [1, -34],
        shadowSize: [41, 41]
      });
      setCustomIcon(icon);
    };
    initLeaflet();
    fetchIpData();
  }, []);

  const fetchIpData = async (ipToLookup: string = '') => {
    setLoading(true);
    try {
      const url = ipToLookup ? `/api/my-ip?ip=${ipToLookup}` : '/api/my-ip';
      const res = await fetch(url);
      const json = await res.json();

      if (json.error) {
        setData({ ...json, ip: ipToLookup || 'Unknown', error: json.error });
      } else {
        setData(json);
        if (!ipToLookup) setInputIp(json.ip);
      }
    } catch (error) {
      console.error("Error fetching IP", error);
      setData({ error: 'Gagal menghubungi server.', ip: ipToLookup || '-', lat: 0, lon: 0 } as IpData);
    } finally {
      setLoading(false);
    }
  };

  const handleLookup = (e: React.FormEvent) => {
    e.preventDefault();
    if (inputIp) fetchIpData(inputIp);
  };

  return (
    <div className="flex min-h-screen flex-col bg-slate-50 font-sans">
      
      {/* --- HEADER (ALIGNED LEFT WITH CONTENT) --- */}
      <header className="sticky top-0 z-50 flex h-20 items-center border-b border-slate-100 bg-white">
        <div className="mx-auto flex w-full max-w-6xl items-center gap-2 px-4 md:gap-3 md:px-6">
          {/* Logo IDnity - md:w-39.5 is the canonical class for 158px */}
          <div className="relative h-7 w-26 md:h-8 md:w-39.5">
            <Image 
              src="/images/logo.png" 
              alt="IDnity Logo"
              fill
              className="object-contain object-left"
              priority
            />
          </div>
          
          <span className="text-xl font-light text-slate-300">|</span>
          
          <h1 className="whitespace-nowrap text-sm font-bold tracking-tight text-slate-900 md:text-base">
            Check IP Address
          </h1>
        </div>
      </header>

      {/* --- MAIN CONTENT --- */}
      <main className="mx-auto w-full max-w-6xl grow py-10 px-4 md:px-6">
        
        {/* --- SEARCH BOX (FULL WIDTH) --- */}
        <div className="mb-6 w-full">
            <div className="rounded-xl border border-slate-200 bg-white p-6 shadow-sm">
                <label className="mb-3 block text-sm font-medium text-slate-600 md:text-base">
                    IP Address to Check
                </label>
                <form onSubmit={handleLookup} className="flex gap-3">
                    <input 
                        type="text" 
                        placeholder="8.8.8.8"
                        className="flex-1 rounded-lg border border-slate-300 px-4 py-3 text-slate-700 outline-none transition focus:border-blue-500 focus:ring-2 focus:ring-blue-500"
                        value={inputIp}
                        onChange={(e) => setInputIp(e.target.value)}
                    />
                    <button 
                        type="submit"
                        disabled={loading}
                        className="flex items-center gap-2 whitespace-nowrap rounded-lg bg-blue-600 px-6 py-3 font-medium text-white transition hover:bg-blue-700 disabled:opacity-70"
                    >
                        {loading ? <Loader2 className="animate-spin" size={20} /> : <Search size={20} />}
                        Lookup
                    </button>
                </form>
            </div>
        </div>

        {/* Content Area */}
        {data && (
            <div className="grid grid-cols-1 items-start gap-6 lg:grid-cols-12">
                
                {/* LEFT COLUMN: Data Details */}
                <div className="flex flex-col gap-4 lg:col-span-5">
                    <div className="overflow-hidden rounded-xl border border-slate-200 bg-white shadow-sm">
                        <div className="flex items-center justify-between border-b border-slate-100 bg-slate-50 px-6 py-4">
                            <span className="text-sm font-semibold uppercase tracking-wider text-slate-500">Result Details</span>
                            {data.error ? (
                                <span className="rounded-full bg-red-100 px-2 py-1 text-xs font-bold text-red-600">Error</span>
                            ) : (
                                <span className="flex items-center gap-1 rounded-full bg-green-100 px-2 py-1 text-xs font-bold text-green-700">
                                    <span className="h-2 w-2 animate-pulse rounded-full bg-green-500"></span> Live
                                </span>
                            )}
                        </div>
                        
                        {data.error ? (
                             <div className="p-6 text-center text-red-500">
                                <p>{data.error}</p>
                             </div>
                        ) : (
                            <div className="divide-y divide-slate-100">
                                <DetailRow icon={<Network className="text-blue-500" />} label="IP Address" value={data.ip} isHighlight />
                                <DetailRow icon={<Server className="text-purple-500" />} label="ISP Name" value={data.isp} />
                                <DetailRow icon={<Shield className="text-orange-500" />} label="AS Number" value={data.asn} />
                                <DetailRow icon={<Globe className="text-cyan-500" />} label="Country" value={data.country} />
                                <DetailRow icon={<MapPin className="text-red-500" />} label="Region/City" value={`${data.region}, ${data.city}`} />
                                <DetailRow icon={<Globe className="text-slate-400" />} label="Timezone" value={data.timezone} />
                            </div>
                        )}
                    </div>
                </div>

                {/* RIGHT COLUMN: Map (h-125 is the canonical class for 500px) */}
                <div className="lg:col-span-7">
                    <div className="relative z-0 h-125 rounded-xl border border-slate-200 bg-white p-1 shadow-sm">
                        {data.lat && data.lon && customIcon ? (
                             <MapContainer 
                                center={[data.lat, data.lon]} 
                                zoom={13} 
                                scrollWheelZoom={false} 
                                className="z-0 h-full w-full rounded-lg"
                             >
                                <TileLayer
                                    attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
                                    url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
                                />
                                <Marker position={[data.lat, data.lon]} icon={customIcon}>
                                    <Popup className="font-sans text-sm">
                                        <strong>{data.isp}</strong><br />
                                        {data.city}, {data.country}<br/>
                                        IP: {data.ip}
                                    </Popup>
                                </Marker>
                             </MapContainer>
                        ) : (
                            <div className="flex h-full w-full flex-col items-center justify-center rounded-lg bg-slate-50 text-slate-400">
                                <Globe size={48} className="mb-4 opacity-20" />
                                <p>{loading ? 'Memuat Peta...' : 'Peta lokasi tidak tersedia'}</p>
                            </div>
                        )}
                    </div>
                </div>

            </div>
        )}
      </main>

      {/* --- FOOTER --- */}
      <footer className="mt-auto border-t border-slate-200 bg-white py-6">
          <div className="mx-auto px-6 text-center text-sm text-slate-500">
              <p>&copy; {new Date().getFullYear()} <strong>IDnity Tools</strong>. Part of IDnity Ecosystem.</p>
          </div>
      </footer>
    </div>
  );
}

function DetailRow({ icon, label, value, isHighlight = false }: { icon: React.ReactNode, label: string, value: string | number, isHighlight?: boolean }) {
    return (
        <div className="group flex items-center p-4 transition hover:bg-slate-50">
            {/* Using canonical border-black/8 if needed, but slate-100 is cleaner here */}
            <div className="mr-4 flex h-9 w-9 items-center justify-center rounded-lg border border-slate-100 bg-slate-50 transition group-hover:border-slate-200 group-hover:bg-white">
                <div className="flex h-5 w-5 items-center justify-center">
                    {icon}
                </div>
            </div>
            <div className="min-w-0 flex-1">
                <p className="mb-0.5 text-xs font-semibold uppercase text-slate-400">{label}</p>
                <p className={`truncate text-sm ${isHighlight ? 'font-mono text-base font-bold text-blue-700' : 'font-medium text-slate-800'}`}>
                    {value || '-'}
                </p>
            </div>
        </div>
    );
}